import React ,{useEffect, useState} from 'react';

const Header = () => {

    

    useEffect(() => {

        document.addEventListener('scroll', function() {
                    weeling()
        })
    }, []);
    

  const weeling = () => {
    const header = document.querySelector('.header');
    
    if (window.scrollY > 0){
        header?.classList.add('header-background');
    }else{
        header?.classList.remove('header-background');
    }
  }
  const headerToggle = () =>{
const body :any= document.querySelector('body');
const head :any = document.querySelector('.head')
console.log(head.offsetTop);
if (head.offsetTop < 0 && window.innerWidth < 900){
    head.style.top = '0vh'
    body.style.height = '100vh'
}
else if (head.offsetTop >= -((6*100)/window.innerHeight) && window.innerWidth < 900){
    head.style.top = '-120vh'
    body.style.height = '100%'
}

  }
  
    return (
        <div  className="header d-flex align-items-center justify-content-around flex-rox w-100 ">
            <span onClick={headerToggle} className="logo-container d-flex align-items-center"><span className="logo" /></span>
            <span className="head d-flex align-items-center justify-content-around w-100 ">
            <div >Home</div>
            <div >WoodWorking</div>
            <div >Speaker</div>
            <div >Electronics</div>
           
            </span>
        </div>
    );
}

export default Header;
