import React from 'react';

const Home = () => {

    const scrollDown = async () =>{
       const scrollTo= document.querySelector(".body1")
       scrollTo?.scrollIntoView()
    }
    

    return (
        <div className="home justify-content-around">
            <div onClick={scrollDown} className="arrow-down" />
        </div>
    );
}

export default Home;
