import { count } from 'console';
import React, { useEffect, useState } from 'react';
let bufferCounter : number = 0
const Counter = () => {

    const [counter ,setCounter] =useState<number>(0)
    useEffect(() => {
        const counterElement : any= document.querySelector('.counter')
        
        document.addEventListener('scroll', function () {
            
            if(window.pageYOffset > counterElement?.offsetTop - window.innerHeight && bufferCounter === 0 ){
                
                count()
            }
        })
    }, []);

    const sleep = (m: number) => new Promise(r => setTimeout(r, m))

const count = async() => {
    
    while (bufferCounter < 1226){
        bufferCounter += 4
        setCounter(bufferCounter)
        await sleep(10)
    }
}



    return (
        <div className="counter d-flex flex-row justify-content-around align-items-center">
            <div>
                <h2 className ="text-center">{counter}</h2>
                <h3>Projects</h3>
            </div>
            <div>
                <h2 className ="text-center">0</h2>
                <h3>Injuries</h3>
            </div>
        </div>
    );
}

export default Counter;
